import useWindowSize from "../../../hooks/useWindowSize";
import useValue from "../../../hooks/useValue";
import { Grid } from "@nextui-org/react";
import FooterInfoBlock from "./FooterInfoBlock";

const FooterInformation = ({blocks}) => {
    const windowSize = useWindowSize();
    const mobileVersion = windowSize.width < 1280;
    const currentExpanded = useValue("");

    const additionalHelpItems = [
        {text: " ", link: "https://www.lipsum.com/feed/html"},
        {text: " ", link: "https://www.lipsum.com/feed/html"},
        {text: "", link: "https://www.lipsum.com/feed/html"},
    ]

    const onOpen = id => {
        if (currentExpanded.value === id) {
            currentExpanded.set("");
            return;
        }

        currentExpanded.set(id);
    }

    console.log(currentExpanded.value);
    return (
        <Grid.Container>
            <FooterInfoBlock
                title={""}
                items={[
                    {text: "/: 8 (861) 373-83-91"},
                    {text: ": 8 (861) 373-83-94"},
                    {text: ": 8 (861) 373-20-87"},
                    {text: ": 8 (861) 373-20-40"},
                    {text: ": arm_teatr@inbox.ru"},
                ]}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "contacts"}
                onOpen={() => onOpen("contacts")}
            />
            <FooterInfoBlock
                title={" "}
                items={[
                    {text: ": "},
                    {text: "-: c 10:30  18:00"},
                    {text: ": c 13:00  14:00"},
                ]}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "time"}
                onOpen={() => onOpen("time")}
            />
            <FooterInfoBlock
                title={""}
                items={[
                    {text: " "},
                    {text: ". , . , . 84"},
                ]}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "address"}
                onOpen={() => onOpen("address")}
            />
            <FooterInfoBlock
                title={""}
                items={[
                    {text: " ", link: "https://www.lipsum.com/feed/html"},
                    {text: "  ", link: "https://www.lipsum.com/feed/html"},
                    {text: " ", link: "https://www.lipsum.com/feed/html"},
                    {text: " ", link: "https://www.lipsum.com/feed/html"},
                    {text: " ", link: "https://www.lipsum.com/feed/html"},
                ]}
                additionalItems={mobileVersion ? additionalHelpItems : null}
                mobileVersion={mobileVersion}
                isOpen={currentExpanded.value === "help"}
                onOpen={() => onOpen("help")}
            />
            {!mobileVersion &&
                <FooterInfoBlock
                    title={""}
                    items={additionalHelpItems}
                    mobileVersion={mobileVersion}
                />
            }

        </Grid.Container>
    )
        ;
}

export default FooterInformation;